:root {
    --primary-color: #051622;
    --secondary-color: #1BA098;
    --tirtiary-color: #DEB992;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: var(--primary-color);
    color: var(--secondary-color);
    font-family: 'Tajawal', sans-serif;
}

/* SECTION I - NAVBAR */

#navbar {
    height: 100vh;
    width: 20%;
    border-right: 2px solid var(--secondary-color);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
}

#navbar ul {
    list-style: none;
}

#navbar ul li {
    margin-top: 25px;
    font-size: 1.3rem;
}

#navbar ul li a {
    text-decoration: none;
    color: inherit;
}

#navbar ul li a:hover {
    border-bottom: 2px solid var(--secondary-color);
}

/* Container holds all the website content except Navbar*/

#container {
    margin-left: 20%;
}

/* SECTION II - MAIN */

#cards {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 100px 0;
    flex-direction: column;
}

.color-card {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 30px;
}

.color {
    color: #fff;
    height: 80px;
    width: 800px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .2s linear;
}

.color:hover {
    transform: scale(1.05);
}

/* UTILITY CLASSES */

.left{
    margin-right: auto;
    margin-bottom: 10px;
}

@media only screen and (max-width: 1050px) {
    .color {
        width: 500px;
    }
  }

@media only screen and (max-width: 1050px) {
    #navbar {
        display: none;
    }
    #container {
        margin: 0;
    }
  }

  @media only screen and (max-width: 520px) {
    .color {
        width: 200px;
    }
  }